import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { LunaPlatformComponent } from './components/luna-platform/luna-platform.component';
import { LunaSdkComponent } from './components/luna-sdk/luna-sdk.component';
import { FaceDjComponent } from './components/face-dj/face-dj.component';
import { LunaCarsComponent } from './components/luna-cars/luna-cars.component';
import { ProductsComponent } from './products/products.component';
import { RouterModule, Routes } from '@angular/router';

export const ROUTES: Routes = [
  {
    path: '',
    children: [
      { path: '', component: ProductsComponent },
      { path: 'face-dj', component: FaceDjComponent },
      { path: 'luna-cars', component: LunaCarsComponent },
      { path: 'luna-platform', component: LunaPlatformComponent },
      { path: 'luna-sdk', component: LunaSdkComponent }
    ]
  }
];

@NgModule({
  imports: [
    CommonModule,
    RouterModule.forChild(ROUTES)
  ],
  declarations: [LunaPlatformComponent, LunaSdkComponent, FaceDjComponent, LunaCarsComponent, ProductsComponent]
})
export class ProductsModule { }
