import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SecurityComponent } from './components/security/security.component';
import { RetailComponent } from './components/retail/retail.component';
import { BankingComponent } from './components/banking/banking.component';
import { TransportationComponent } from './components/transportation/transportation.component';
import { IndustriesComponent } from './industries/industries.component';
import {RouterModule, Routes} from '@angular/router';

export const ROUTES: Routes = [
  {
    path: '',
    children: [
      { path: '', component: IndustriesComponent },
      { path: 'banking', component: BankingComponent },
      { path: 'retail', component: RetailComponent },
      { path: 'security', component: SecurityComponent },
      { path: 'transportation', component: TransportationComponent }
    ]
  }
];

@NgModule({
  imports: [
    CommonModule,
    RouterModule.forChild(ROUTES)
  ],
  declarations: [SecurityComponent, RetailComponent, BankingComponent, TransportationComponent, IndustriesComponent]
})
export class IndustriesModule { }
